methods: {

onChange (val) {
console.log('on-change', val);
this.initData(val);
let time = val;
let now = new Date();
if (time === formatDateTime(now)) {
this.yearTime = new Date(time).getFullYear();
this.monthTime = new Date(time).getMonth();
this.dayTime = new Date(time).getDate();
this.getPunchList(val);
} else {
this.yearTime = new Date(time).getFullYear();
this.monthTime = new Date(time).getMonth();
this.dayTime = new Date(time).getDate();
this.getPunchList(val);
}
},

choose(){
if (this.isPunch === true) {
this.isPunch = false;
} else {
this.isPunch = true;
}
},

formatDate (year, month, day) {
const y = year;
let m = month;
if (m < 10) m = `0${m}`;
let d = day;
if (d < 10) d = `0${d}`;
return `${y}-${m}-${d}`
},

initData (cur) {
let date = '';
if (cur) {
date = new Date(cur)
} else {
date = new Date()
}
this.currentDay = date.getDate();          // 今日日期 几号
this.currentYear = date.getFullYear();     // 当前年份
this.currentMonth = date.getMonth() + 1;  // 当前月份
this.currentWeek = date.getDay();// 1...6,0   // 星期几
const str = this.formatDate(this.currentYear, this.currentMonth, this.currentDay)// 今日日期 年-月-日
this.days.length = 0;

for (let i = this.currentWeek; i >= 0; i -= 1) {
const d = new Date(str);
d.setDate(d.getDate() - i);
this.days.push(d)
}

for (let i = 1; i <= 6 - this.currentWeek; i += 1) {
const d = new Date(str);
d.setDate(d.getDate() + i);
this.days.push(d)
}
},

weekPre () {
const d = this.days[0]; // 如果当期日期是7号或者小于7号
d.setDate(d.getDate() - 7);
this.initData(d)
},


weekNext () {
const d = this.days[6];  // 如果当期日期是7号或者小于7号
d.setDate(d.getDate() + 7);
this.initData(d)
},


pickPre (year, month) {
const d = new Date(this.formatDate(year, month, 1));
d.setDate(0);
this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1))
},


pickNext (year, month) {
const d = new Date(this.formatDate(year, month, 1));
d.setDate(35);
this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1))
},


pick (date) {
let time = this.formatDate(date.getFullYear(), date.getMonth() + 1, date.getDate());
this.initData(this.formatDate(date.getFullYear(), date.getMonth() + 1, date.getDate()));
this.yearTime = new Date(time).getFullYear();
this.monthTime = new Date(time).getMonth();
this.dayTime = new Date(time).getDate();
this.getPunchList(time);
},


backToday(){
let date = new Date();
let time = this.formatDate(date.getFullYear(), date.getMonth() + 1, date.getDate());
this.initData(this.formatDate(date.getFullYear(), date.getMonth() + 1, date.getDate()));
this.yearTime = new Date(time).getFullYear();
this.monthTime = new Date(time).getMonth();
this.dayTime = new Date(time).getDate();
this.getPunchList(time);
},


startPunchCard(type){
let _this = this;
this.$wechat.getLocation({
type: 'wgs84', // gps坐标
success: function (res) {
let latitude = res.latitude;
let longitude = res.longitude;
let params = {
/* "hrEmpId": "123",*/
"hrLongitude": longitude,
"hrLatitude": latitude,
"hrClockType": type,
};
checkClockByPlace(params).then(response => {
_this.punchCardLoading = false;
_this.$vux.loading.hide();
if (response.data.success) {
_this.getClockTime();
let now = new Date();
_this.getPunchList(formatDateTime(now));
_this.$vux.toast.text('打卡成功', 'middle');
} else {
_this.$vux.toast.text(response.data.message, 'middle');
}
}).catch(error => {
console.log(error);
_this.$vux.loading.hide();
_this.$vux.toast.text('打卡失败', 'middle');
});
}
});
},


