appstartPunchCard(type){
let _this = this;


function onSuccess(position) {
let latitude = position.coords.latitude;
let longitude = position.coords.longitude;
let params = {
"hrLongitude": longitude,
"hrLatitude": latitude,
"hrClockType": type,
};
checkClockByPlace(params).then(response => {
_this.punchCardLoading = false;
_this.$vux.loading.hide();
if (response.data.success) {
_this.getClockTime();
let now = new Date();
_this.getPunchList(formatDateTime(now));
_this.$vux.toast.text('打卡成功', 'middle');
console.log("打卡成功");
} else {
_this.$vux.toast.text(response.data.message, 'middle');
}
}).catch(error => {
console.log(error);
_this.$vux.loading.hide();
_this.$vux.toast.text('打卡失败', 'middle');
});
}


function onError(error) {
alert('code: ' + error.code + '\n' + 'message: ' + error.message + '\n');
}
navigator.geolocation.getCurrentPosition(onSuccess, onError);

},


getPunchList(value){
let params = {"hrEmpId": "123", "hrClockTime": value,};
fclockinfoQuery(params).then(response => {
if (response.data.success) {
this.isLoadingDataNo = false;
this.isLoadingData = false;
this.punchHistory = response.data.rows;
this.$vux.loading.hide();
if (response.data.total === 0) {
this.isLoadingDataNo = true;
}
} else {
this.isLoadingDataNo = true;
this.$vux.loading.hide();
this.isLoadingData = false;
}
}).catch(error => {
this.$vux.loading.hide();
this.isLoadingDataNo = true;
this.isLoadingData = false;
});
},

getClockTime(){
clockTime({"hrEmpId": "123"}).then(response => {
if (response.data.success) {
this.$vux.toast.text(response.data.message, 'middle');
this.signNum = response.data.message;
if (this.signNum === "打卡次数剩余0次") {
this.isPunchOver = false;
}
} else {
}
}).catch(error => {
console.log(error);
})
},

sign(){
