return;
} else {
this.$vux.loading.show({text: '打卡中'});
this.isClick = false;
setTimeout(() => {
this.$vux.loading.hide()
}, 2000);
if (this.wxPunch === true) {
setTimeout(() => {
this.startPunchCard('startWork');
this.isClick = true;
}, 2000);
} else if (this.ddPunch === true) {
setTimeout(() => {
this.ddstartPunchCard('startWork');
this.isClick = true;
}, 2000);
} else {
setTimeout(() => {
this.appstartPunchCard('startWork');
this.isClick = true;
}, 2000);
}
}
} else {
if (this.signNum === "打卡次数剩余0次") {
this.$vux.toast.show({
type: "warn",
text: '亲，打卡次数用完啦QAQ~~'
});
