countClock(){
let _this = this;
let timer = setInterval(updateTime, 1000);

function updateTime() {
let cd = new Date();
_this.time = _this.zeroPadding(cd.getHours(), 2) + ':' + _this.zeroPadding(cd.getMinutes(), 2) + ':' + _this.zeroPadding(cd.getSeconds(), 2);
}
},
zeroPadding(num, digit) {
let zero = '';
for (let i = 0; i < digit; i++) {
zero += '0';
}
return (zero + num).slice(-digit);
},


}
