import Vue from 'vue';
import {fetchCode} from './fetch';

/**
 * @getCode function from the wxURL:
 * eg. url: http://handhrhap.hand-china.com/dist/?code=zCGiYN30SxNDeyN7yLn2n3iPC8mDPi0ZigeHDWL3L7U&state=STATE
 * 获取url中的code字段的信息
 *
 *
 * @ddConfig function
 * //dd.config方法会对参数进行验证
 */

let DD_CONFIGS = {
  getCode() {
    let ddUrlObj = {};
    let urlParamsArray = window.location.search.substring(1).split('&');

    urlParamsArray.forEach((item, index, array) => {
      let pos = item.indexOf('=');
      if (pos == '-1') {
      } else {
        ddUrlObj[item.split('=')[0]] = item.split('=')[1];
      }
    });

    return ddUrlObj.code;
  },

  ddConfig(ddUrl) {
    fetchCode({
      url: process.env.baseUrl + 'api/public/getDdJSSDKParams',
      method: 'post',
      data: {
        url: ddUrl
      }
    }).then(function (res) {
      const data = res.data.rows[0] || [];
      window.localStorage.setItem('corpId', data.appId);
      dd.config({
        agentId: "115509997", // 必填，微应用ID
        corpId: data.appId,//必填，企业ID
        timeStamp: data.timestamp, // 必填，生成签名的时间戳
        nonceStr: data.nonceStr, // 必填，生成签名的随机串
        signature: data.signature, // 必填，签名
        type: 0,   //选填。0表示微应用的jsapi,1表示服务窗的jsapi。不填默认为0。该参数从dingtalk.js的0.8.3版本开始支持
        jsApiList: ['device.geolocation.get','runtime.permission.requestAuthCode','biz.util.uploadImage'] // 必填，需要使用的jsapi列表，注意：不要带dd。
      });
    });
  }
};

export default DD_CONFIGS;





